/* Premium Background with Radial Gradient Glow */
body {
  background: linear-gradient(180deg, #FAFBFC 0%, #FFFFFF 100%);
  min-height: 100vh;
  position: relative;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 1400px;
  height: 600px;
  background: radial-gradient(ellipse at top, rgba(37, 99, 235, 0.08) 0%, rgba(124, 58, 237, 0.05) 30%, transparent 70%);
  pointer-events: none;
  z-index: 0;
}

body::after {
  content: '';
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40vh;
  background: radial-gradient(ellipse at bottom, rgba(124, 58, 237, 0.04) 0%, transparent 70%);
  pointer-events: none;
  z-index: 0;
}

